@import "https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

body {
  background-color: #fff;
}

.stopWatch {
  max-width: 600px;
  text-align: center;
  color: #fff;
  background-image: linear-gradient(#000c, #000c), url("background.7150974c.png");
  background-position: center;
  background-size: cover;
  margin: 50px auto 100px;
  padding: 40px 0;
}

.stopWatch h1 {
  margin: 50px 0;
  font-size: 80px;
}

.controls {
  justify-content: center;
  align-items: center;
  display: flex;
}

.stopWatch img {
  width: 60px;
  cursor: pointer;
  margin: 0 30px;
}

.stopWatch img:nth-child(2) {
  width: 90px;
}

